.container {
  margin-top: 5%;
}



html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, "Helvetica Neue", sans-serif;
}

.card {
  background: var(--surface-card);
  padding: 2rem;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.rounded-input {
  border-radius: 8px; 
  border: 1px solid #ccc; 
  padding: 8px 12px;
  font-size: 1rem; 
  outline: none; 
  transition: border-color 0.3s, box-shadow 0.3s; 
}

.rounded-input:focus {
  border-color: #2563EB; 
  box-shadow: 0 0 0 3px rgba(38, 143, 255, 0.25); 
}


